import { Component, OnInit } from '@angular/core';
import { DetailsFormBase } from '../../../../../shared/ui/objects-view/details-form-base';
import { FormBuilder, Validators } from '@angular/forms';
import { IServiceModel } from '../../../../../shared/fleio-api/billing/model/service.model';
import { ClientsApiService } from '../../../../../shared/fleio-api/client-user/client/clients-api.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfigService } from '../../../../../shared/config/config.service';
import { ServicesApiService } from '../../../../../shared/fleio-api/billing/services/service-api.service';
import { Observable, of } from 'rxjs';
import { IActionResult } from '../../../../../shared/ui/objects-view/interfaces/actions/action-result';
import { IProductModel } from '../../../../../shared/fleio-api/billing/model/product.model';
import { IProductCycleModel } from '../../../../../shared/fleio-api/billing/model/product-cycle.model';

@Component({
  selector: 'app-service-edit-form',
  templateUrl: './service-edit-form.component.html',
  styleUrls: ['./service-edit-form.component.scss']
})
export class ServiceEditFormComponent extends DetailsFormBase<IServiceModel> implements OnInit {
  serviceForm = this.formBuilder.group({
    display_name: ['', Validators.required],
    cycle: ['', Validators.required],
    notes: [''],
    override_price: [''],
    override_suspend_until: [''],
    next_due_date: [''],
    next_expiration_date: [''],
    next_invoice_date: [''],
  });

  constructor(
    private formBuilder: FormBuilder,
    private clientsApi: ClientsApiService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private config: ConfigService,
    private servicesApi: ServicesApiService,
  ) {
    super();
  }

  ngOnInit() {
    this.objectController.actionCallback = () => this.saveService();
    this.serviceForm.patchValue(this.object);
  }

  private saveService(): Observable<IActionResult> {
    const value = this.serviceForm.value as IServiceModel;
    value.status = this.object.status;
    value.product = (this.object.product as IProductModel).id;
    value.cycle = (this.object.cycle as IProductCycleModel).id;

    this.createOrUpdate(this.servicesApi, value).subscribe(() => {
      this.router.navigateByUrl(
        this.config.getPrevUrl('billing/services')
      ).catch(() => {
      });
    });

    return of(null);
  }
}
