import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { IBaseFleioObjectModel } from '../../../shared/fleio-api/base-model/base-fleio-object.model';
import { IObjectListUIService } from '../../../shared/ui/objects-view/interfaces/object-list-ui-service';
import { IPermissionsModel } from '../../../shared/fleio-api/base-model/IPermissionsModel';
import { ConfigService } from '../../../shared/config/config.service';
import { IObjectUIService } from '../../../shared/ui/objects-view/interfaces/object-ui-service';
import { ITableData } from '../../../shared/ui/objects-view/interfaces/table-data/table-data';
import { FleioObjectsList } from '../../../shared/fleio-api/fleio-objects-list';
import { ITableRow } from '../../../shared/ui/objects-view/interfaces/table-data/table-row';
import { ColumnType } from '../../../shared/ui/objects-view/interfaces/table-data/column-definition';
import { ServicesApiService } from '../../../shared/fleio-api/billing/services/service-api.service';
import { ServiceUIService } from './service-ui.service';
import { IServiceModel } from '../../../shared/fleio-api/billing/model/service.model';
import { IAction } from '../../../shared/ui/objects-view/interfaces/actions/action';
import { DatePipe } from '@angular/common';

@Injectable({
  providedIn: 'root',
})
export class ServiceListUIService implements IObjectListUIService {
  private datePipe: DatePipe;

  constructor(private router: Router, private config: ConfigService, private servicesApiService: ServicesApiService) {
    this.datePipe = new DatePipe(this.config.locale);
  }

  getObjectUIService(
    object: IBaseFleioObjectModel, permissions: IPermissionsModel, state: string
  ): IObjectUIService {
    return new ServiceUIService(
      object as IServiceModel, permissions, state, this.router, this.config, this.servicesApiService,
    );
  }

  getTableData(objectList: FleioObjectsList<IServiceModel>): ITableData {
    const tableData: ITableData = {
      header: {
        columns: [
          {type: ColumnType.Value, displayName: 'Name', enableSort: true, fieldName: 'name'},
          {type: ColumnType.Value, displayName: 'Client', enableSort: true, fieldName: 'client'},
          {type: ColumnType.Value, displayName: 'Created at', enableSort: true, fieldName: 'created_at'},
          {type: ColumnType.Value, displayName: 'Next due date', enableSort: true, fieldName: 'next_due_date'},
          {type: ColumnType.Value, displayName: 'Status', enableSort: true, fieldName: 'status'},
          {type: ColumnType.Actions, displayName: 'Actions', enableSort: true, fieldName: '(actions)'},
        ],
        columnNames: ['name', 'client', 'created_at', 'next_due_date', 'status', '(actions)'],
        statusColumn: 'name',
      },
      rows: [],
    };

    for (const service of objectList.objects) {
      const rowUIService = this.getObjectUIService(service, objectList.permissions, 'table-view');
      const row: ITableRow = {
        cells: {
          name: {text: service.display_name},
          client: {
            text: service.client.name,
            url: this.config.getPanelUrl(`clients-users/clients/${service.client.id}`)
          },
          created_at: {text: this.datePipe.transform(service.created_at)},
          next_due_date: {text: this.datePipe.transform(service.next_due_date)},
          status: {text: service.status.toLocaleUpperCase()},
        },
        icon: rowUIService.getIcon(),
        status: rowUIService.getStatus(),
        actions: rowUIService.getActions(),
        url: rowUIService.getDetailsLink()
      };

      tableData.rows.push(row);
    }

    return tableData;
  }

  getActions(objectList: FleioObjectsList<IBaseFleioObjectModel>): IAction[] {
    return null;
  }
}
