import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { IRouteConfig } from '../../../shared/ui-api/interfaces/route-config/route-config';
import { ServiceListComponent } from './service-list/service-list.component';
import { ServiceDetailsComponent } from './service-details/service-details.component';
import { ServiceEditComponent } from './service-edit/service-edit.component';
import { ServiceListResolver } from '../../../shared/fleio-api/billing/services/service-list.resolver';
import { ServiceResolver } from '../../../shared/fleio-api/billing/services/service.resolver';
import { FleioObjectsList } from '../../../shared/fleio-api/fleio-objects-list';
import { IBaseFleioObjectModel } from '../../../shared/fleio-api/base-model/base-fleio-object.model';
import { OrderingDirection } from '../../../shared/ui-api/interfaces/route-config/ordering-directions';
import { AuthGuard } from '../../../shared/auth/auth.guard';

const routes: Routes = [
  {
    path: '',
    component: ServiceListComponent,
    resolve: {
      services: ServiceListResolver,
    },
    canActivate: [AuthGuard],
    data: {
      config: {
        feature: 'billing.services',
        search: {
          show: true,
          placeholder: 'Search services ...',
        },
        subheader: {
          objectNamePlural: 'services',
          objectName: 'service',
          objectList(data): FleioObjectsList<IBaseFleioObjectModel> {
            return data.services;
          }
        },
        ordering: {
          default: {
            field: 'created_at',
            display: 'Created at',
            direction: OrderingDirection.Descending,
          },
          options: [
            {display: 'Name', field: 'name'},
            {display: 'Client', field: 'client'},
            {display: 'Created at', field: 'created_at'},
            {display: 'Next due date', field: 'next_due_date'},
            {display: 'Status', field: 'status'},
          ]
        }
      } as IRouteConfig,
    },
    runGuardsAndResolvers: 'always'
  },
  {
    path: ':id',
    component: ServiceDetailsComponent,
    resolve: {
      service: ServiceResolver,
      // permissions: ServicePermissionsResolver,
    },
    data: {
      config: {
        getBreadCrumbDetail: (data) => {
          return data.service.name;
        },
      } as IRouteConfig,
    }
  },
  {
    path: ':id/edit',
    component: ServiceEditComponent,
    resolve: {
      service: ServiceResolver,
    },
    data: {
      config: {
        getBreadCrumbDetail: (data) => {
          return data.service.name;
        },
      } as IRouteConfig,
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ServicesRoutingModule {
}
